//layout mixins - start

@mixin eltdTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin eltdTableCellLayout() {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: middle;
}

@mixin eltdRelativeHolderLayout() {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: middle;
}

@mixin eltdAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin eltdTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

//layout mixins - end

//transition mixins - start

@mixin eltdTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin eltdTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin eltdTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin eltdAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin eltdTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

//transition mixins - end

//checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;
%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;
    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }
    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }
    input[type=checkbox] + label span.eltd-label-text {
        padding-left: 10px;
        display: inline-block;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }
    input[type=checkbox] + label .eltd-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid $default-border-color;
        background-color: $default-background-color;
        border-radius: 2px;
        &:hover {
            cursor: pointer;
        }
    }
    input[type=checkbox] + label .eltd-label-view:after {
        content: '';
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        background-color: $first-main-color;
        opacity: 0;
        @include eltdTransition(opacity 0.3s ease-in-out);
    }
    input[type=checkbox]:checked + label .eltd-label-view:after {
        opacity: 1;
    }
}

//checkbox mixins - end

/* common mixins - start */

@mixin eltdBckImageStyle(){
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin eltdImageOverlayHoverStyle($with-hover: true){
    
    @if ($with-hover) {
        
        &:hover {
        
            &:after {
                opacity: 1;
            }
        }
    
        &:after {
            @include eltdAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-background-color, .15);
            opacity: 0;
            @include eltdTransition(opacity .2s ease-in-out);
        }
        
    } @else {
        @include eltdAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-background-color, .15);
        opacity: 0;
        @include eltdTransition(opacity .2s ease-in-out);
    }
}

@mixin eltdButtonDefaultStyle() {
    font-family: $default-heading-font;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    outline: none;
    font-size: 13px;
    line-height: 2em;
    letter-spacing: 0.05em;
    font-weight: 600;
    text-transform: uppercase;
    box-sizing: border-box;
    margin: 0;
    @include eltdTransition(all .15s);
}

@mixin eltdGradientColor($gradient-param...) {
    background: -o-linear-gradient($gradient-param);
    background: -moz-linear-gradient($gradient-param);
    background: -webkit-linear-gradient($gradient-param);
    background: linear-gradient($gradient-param);
}

@mixin eltdButtonBackground($light: false) {
    @include eltdAbsoluteHolderLayout();
    top: 7px;
    left: 7px;
    z-index: 0;

    @if($light == true){
        @include eltdGradientColor(
                        -45deg,
                        rgba(255, 255, 255, 1),
                        rgba(255, 255, 255, 1) 5.5555555556%,
                        rgba(255, 255, 255, 0) 5.5555555556%,
                        rgba(255, 255, 255, 0) 50%,
                        rgba(255, 255, 255, 1) 50%,
                        rgba(255, 255, 255, 1) 55.5555555556%,
                        rgba(255, 255, 255, 0) 55.5555555556%,
                        rgba(255, 255, 255, 0)
        );
    }
    @else {
        @include eltdGradientColor(
                        -45deg,
                        rgba(0, 0, 0, 1),
                        rgba(0, 0, 0, 1) 5.5555555556%,
                        rgba(0, 0, 0, 0) 5.5555555556%,
                        rgba(0, 0, 0, 0) 50%,
                        rgba(0, 0, 0, 1) 50%,
                        rgba(0, 0, 0, 1) 55.5555555556%,
                        rgba(0, 0, 0, 0) 55.5555555556%,
                        rgba(0, 0, 0, 0)
        );
    }

    background-size: 12px 12px;
    background-repeat: repeat;
    @include eltdTransition(0.15s ease-out);
}

@mixin eltdButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin eltdButtonSolidColor() {
    color: $default-heading-color;
    border: 1px solid transparent;

    .eltd-btn-cover{
        background-color: $first-main-color;
    }
}

@mixin eltdButtonSolidHoverColor() {
    color: $default-heading-color;
    background-color: $first-main-color;
    border-color: transparent;
}

@mixin eltdButtonOutlineColor() {
    color: $default-heading-color;
    background-color: #fff;
    border: 1px solid $default-heading-color;
}

@mixin eltdButtonOutlineHoverColor() {
    color: $default-heading-color;
    background-color: #fff;
    border-color: $default-heading-color;
}

@mixin eltdButtonSmallParams() {
    padding: 17px 38px;
}

@mixin eltdButtonMediumParams() {
    padding: 17px 48px;
}

@mixin eltdButtonLargeParams() {
    padding: 17px 58px;
}

@mixin eltdButtonHugeParams() {
    display: block;
    text-align: center;
    padding: 17px 28px;
}

@mixin eltdPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }
    &:-moz-placeholder {
        @content;
        opacity: 1;
    }
    &::-moz-placeholder {
        @content;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

/* common mixins - end */

/* mixins styles - start */

%input-style {
    width: 100%;
    margin: 0 0 26px;
    padding: 27px 20px;
    font-size: 13px;
    line-height: 19px;
    font-weight: 300;
    font-style: italic;
    font-family: $default-heading-font;
    text-transform: lowercase;
    letter-spacing: 0.08em;
    color: $default-text-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    box-sizing: border-box;
    @include eltdTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-text-color;
        border-color: $default-text-color;
    }

    @include eltdPlaceholder {
        color: inherit;
    }

    &::-webkit-contacts-auto-fill-button {
        visibility: hidden;
        display: none !important;
        pointer-events: none;
        position: absolute;
        right: 0;
    }

}

/* mixins styles - end */

